  transition: transform 2.5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
}

.ring:before {
  box-sizing: content-box;
  content: "";
  width: 100%;
  height: 100%;
  border: 3px solid #ff6b08;
  border-color: #ff6b08 #ff6b08 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: animateCircle 2s linear infinite;
  position: absolute;
  top: -3px;
  left: -3px;
}

.spinner {
  box-sizing: content-box;
  width: 50%;
  height: 4px;
  transform-origin: 0;
  background: none;
  animation: animateDot 2s linear infinite;
  display: block;
  position: absolute;
  top: calc(50% - 2px);
  left: 50%;
}

.spinner:before {
  content: "";
  width: 16px;
  height: 16px;
  background: #ff6b08;
  border-radius: 50%;
  position: absolute;
  top: -6px;
  right: -8px;
  box-shadow: 0 0 20px #fff000;
}

@keyframes animateCircle {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes animateDot {
